import {showToast,get} from '@/utils/index';
import {getSortDetailUrl} from '@/config';
import NovelBook from '@/components/novel-book/index.vue';
export default{
	data(){
		return{
			nomore: false,
			page: 1,
			books: [],
			gender:  '',//男生:male 女生:female 出版:press
			major: '', //主分类
		}
	},
	components: {
		NovelBook
	},
	mounted(){
		let query = this.$mp.query;
		this.gender = query.gender||'male';
		this.major = query.major||'玄幻';
		uni.setNavigationBarTitle({
			title: this.major
		});
		this.getData();
	},
	methods: {
		jumpPage(url){
			if(url && typeof(url)=='string'){
				uni.navigateTo({
					url: url,
					animationType: 'slide-in-bottom'
				});
			}
		},
		initData(){
			this.nomore = false;
			this.page = 1;
			this.books = [];
		},
		getData(){
			if(this.nomore){
				showToast('没有更多');
				return;
			}
			get(getSortDetailUrl, {
				gender: this.gender,
				type: 'hot',
				major: this.major,
				minor: '',
				start: this.page,
				limit: 30,
			})
			.then((res)=>{
				if(res.ok){
					let books = res.books;
					if(books && books.length>0){
						books.forEach((item)=>{
							item.cover = decodeURIComponent(item.cover).replace('/agent/', '');
						});
						this.books = [...this.books, ...books];
					}else{
						this.nomore = true;
					};
					this.page++;
				}else{
					showToast('请求出错');
				}
			})
		}
	},
	onPullDownRefresh() {
		uni.stopPullDownRefresh();
		uni.hideNavigationBarLoading();
		this.initData();
		this.getData();
	},
	onReachBottom() {
		this.getData();
	}
}
